h1,
h2,
h3,
h4,
h5,
h6 {
	clear: both;
}

p {
	margin-bottom: 1.5em;
}

big {
	font-size: 125%;
}

blockquote {
	margin: 0 1.5em;
}

address {
	margin: 0 0 1.5em;
}

a {
	color: $color__link;

	&:visited {
		color: $color__link-visited;
	}

	&:active,
	&:focus,
	&:hover {
		color: $color__link-hover;
	}

	&:focus {
		outline: thin dotted;
	}

	&:active,
	&:hover {
		outline: 0;
	}
}

hr {
	background-color: $color__background-hr;
	border          : 0;
	height          : 1px;
	margin-bottom   : 1.5em;
}

cite,
dfn,
em,
i {
	font-style: italic;
}

pre {
	background   : $color__background-pre;
	font-family  : $font__pre;
	@include font-size(.9375);
	line-height  : $font__line-height-pre;
	margin-bottom: 1.6em;
	max-width    : 100%;
	overflow     : auto;
	padding      : 1.6em;
}

code,
kbd,
tt,
var {
	font-family: $font__code;
	@include font-size(.9375);
}

abbr,
acronym {
	border-bottom: 1px dotted $color__border-abbr;
	cursor       : help;
}

ins,
mark {
	background     : $color__background-ins;
	text-decoration: none;
}